<style lang="less" scoped>
.details {
    padding: 16px;
    thead {
        width: 138px;
        // height: 138px;
        background-color: #efeff3;
        display: block;
        overflow: hidden;
        > img {
            width: 138px;
            display: block;
        }
    }
    tbody {
        font-size: 12px;
        color: #2D2D2D;
    }

    tfoot {
        font-size: 15px;
        .tag {
            font-size: 10px;
            padding: 0 4px;
            background-color: #000;
            color: #fff;
            border-radius: 4px;
        }
        .pric {
            color: #FF5A00;
            margin: 0px 5px;
        }
        s {
            color: #8c8c8c;
        }
    }
}

</style>
<template>
    <div class="details">
        <thead>
            <img :src="attributes.image"/>
        </thead>
        <tbody>
            <div>{{attributes.brand_id}}</div>
            <div>{{attributes.product_name}}</div>
        </tbody>
        <tfoot>
            <span class="tag">3折</span>
            <span class="pric">{{attributes.price_discounted | pric}}</span>
            <s>{{attributes.price_original | pric }}</s>
        </tfoot>
    </div>
</template>
<script>
export default {
    componentName: 'fx-dis-det',
    props: ['attributes']
}
</script>



